    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out; }
  .btn:focus {
    box-shadow: none; }

.btn-solid {
  padding: 13px 29px;
  color: #ffffff !important;
  letter-spacing: 0.05em;
  border: 2px solid #ff4c3b;
  background-image: -webkit-linear-gradient(30deg, #ff4c3b 50%, transparent 50%);
  background-image: -webkit-linear-gradient(60deg, #ff4c3b 50%, transparent 50%);
  background-image: linear-gradient(30deg, #ff4c3b 50%, transparent 50%);
  background-size: 540px;
  background-repeat: no-repeat;
  background-position: 0;
  -webkit-transition: background 300ms ease-in-out;
  transition: background 300ms ease-in-out; }
  .btn-solid:hover {
    background-position: 100%;
    color: #000000 !important;
    background-color: #ffffff; }
  .btn-solid.black-btn {
    background-image: -webkit-linear-gradient(30deg, #222222 50%, transparent 50%);
    background-image: -webkit-linear-gradient(60deg, #222222 50%, transparent 50%);
    background-image: linear-gradient(30deg, #222222 50%, transparent 50%);
    border: 2px solid #222222; }
  .btn-solid:focus {
    color: #ffffff; }

.btn-outline {
  display: inline-block;
  padding: 13px 29px;
  letter-spacing: 0.05em;
  border: 2px solid #ff4c3b;
  position: relative;
  color: #000000; }
  .btn-outline:before {
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
    position: absolute;
    top: 0;
    left: 50%;
    right: 50%;
    bottom: 0;
    opacity: 0;
    content: '';
    background-color: #ff4c3b;
    z-index: -2; }
  .btn-outline:hover {
    color: #ffffff; }
    .btn-outline:hover:before {
      -webkit-transition: 0.5s all ease;
      transition: 0.5s all ease;
      left: 0;
      right: 0;
      opacity: 1; }
  .btn-outline:focus:before {
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
    left: 0;
    right: 0;
    opacity: 1; }

button.btn.btn-solid:active, button.btn.btn-outline:active {
  background-image: -webkit-linear-gradient(30deg, #ff4c3b 50%, transparent 50%);
  background-image: -webkit-linear-gradient(60deg, #ff4c3b 50%, transparent 50%);
  background-image: linear-gradient(30deg, #ff4c3b 50%, transparent 50%);
  color: #ffffff;
  background: #ff4c3b; }

